<template>
  <div class="job-listing">
    <div class="card-container">
      <div 
        v-for="job in paginatedJobs" 
        :key="job.id" 
        class="job-card" 
        :style="{ backgroundColor: getRandomColor() }"
      >
        <h3>{{ job.type }}</h3>
        <p><strong>描述:</strong> {{ job.content }}</p>
        <p><strong>作者:</strong> {{ job.author }}</p>
        <p><strong>电话:</strong> {{ job.phone }}</p>
        <p><strong>微信:</strong> {{ job.wechat }}</p>
        <p><strong>发布日期:</strong> {{ job.date }}</p>
      </div>
    </div>
    <el-pagination
      @current-change="handleCurrentChange"
      @size-change="handlePageSizeChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20]"
      :page-size="pageSize"
      :total="jobs.length"
      layout="total, sizes, prev, pager, next, jumper"
      class="pagination"
    />
  </div>
</template>

<script setup>
import axios from 'axios';
import { computed, onMounted, ref } from 'vue';

const jobs = ref([]);
const currentPage = ref(1);
const pageSize = ref(10); // 每页展示10条数据

// 计算当前页显示的数据
const paginatedJobs = computed(() => {
  const start = (currentPage.value - 1) * pageSize.value;
  return jobs.value.slice(start, start + pageSize.value);
});

const handleCurrentChange = (page) => {
  currentPage.value = page;
};

const handlePageSizeChange = (size) => {
  pageSize.value = size;
  currentPage.value = 1; // 重置为第一页
};

const fetchJobs = async () => {
  try {
    const response = await axios.get('http://localhost:8090/activity', {
      withCredentials: true,
      timeout: 10000
    });
    if (response.data) {
      console.log('获取到的数据:', response.data);
      jobs.value = response.data;
    } else {
      console.error('响应体为空');
    }
  } catch (error) {
    console.error('获取职位数据失败:', error.message);
  }
};

// 随机生成颜色
const getRandomColor = () => {
  const letters = '0123456789ABCDEF';
  let color = '#';
  for (let i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
};

// 页面加载时获取职位数据
onMounted(fetchJobs);
</script>

<style scoped>
html, body {
  height: 100%;
  margin: 0;
}

.job-listing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* 使容器高度为视口高度 */
  padding: 20px;
  font-family: 'Comic Sans MS', cursive, sans-serif; /* 奇葩字体 */
  background: radial-gradient(circle, #ffcc00, #ff6699); /* 奇葩背景 */
  position: relative; /* 为了定位分页 */
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  width: 100%; /* 使卡片容器宽度为100% */
  max-width: 800px; /* 限制最大宽度 */
}

.job-card {
  border: 2px dashed #fff; /* 虚线边框 */
  border-radius: 20px; /* 圆角 */
  padding: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s, box-shadow 0.3s; /* 动画效果 */
  color: #000; /* 字体颜色为黑色 */
  position: relative; /* 为了定位伪元素 */
  overflow: hidden; /* 隐藏溢出内容 */
}

.job-card:hover {
  transform: scale(1.1) rotate(3deg); /* 鼠标悬停时放大和旋转 */
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.8);
}

.job-card h3 {
  margin: 0 0 10px;
  text-shadow: 1px 1px 2px #fff; /* 文字阴影效果 */
}

.job-card p {
  margin: 5px 0;
}

/* 分页样式 */
.pagination {
  position: absolute; /* 固定位置 */
  bottom: 20px; /* 距离底部20px */
  right: 20px; /* 距离左侧20px */
  background: rgba(255, 255, 255, 0.8); /* 半透明背景 */
  border-radius: 10px; /* 圆角 */
  padding: 10px; /* 内边距 */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}
</style>




